import 'antd/dist/antd.css';
import './CourseCard.css';

import {CommentOutlined, FolderOutlined, FormOutlined, NotificationOutlined} from '@ant-design/icons';
import {Col, Dropdown, Image, Menu, message, Row} from 'antd';
import {Link} from 'react-router-dom';
import React from 'react';
import {deleteCourse} from "../../services/courseService";

/*
 * Data structure of props.data:
 * {courseImage:"base64://xxx", courseId:1, courseName: "CSE", courseTerm:"2020-2021 Fall"}
 */

class CourseCard extends React.Component {

    onClick = (e) => {
        console.log(e.key);
    };

    onDelete = () => {
        const {courseId} = this.props.data;
        const data = {
            courseId: courseId,
        };
        const callback = (data) => {
            console.log(data);
            if (data.status === 200) {
                message.success(data.msg);
            } else {
                message.error(data.msg);
            }
        };
        deleteCourse(data, callback);
    }

    render() {
        // eslint-disable-next-line react/prop-types
        const {courseImage, courseId, courseName, courseTerm} = this.props.data;
        const menu = (
            <Menu onClick={this.onClick}>
                <Menu.Item onClick={this.onDelete} key="1">删 除</Menu.Item>
            </Menu>
        );
        return (
            <div className="course-card">
                <div>
                    <div className="image-cover">
                        <Dropdown overlay={menu}>
                            <div className="ant-dropdown-link">
                                <h1 style={{fontSize: 24, color: "#fff", fontWeight: "bold"}}>...</h1>
                            </div>
                        </Dropdown>
                    </div>
                    <Image width={262} height={164} src={courseImage}
                           fallback="data:image/png;base64,"
                    />
                </div>
                <div className="course-content">
                    <Link to={{pathname: '/class', search: '?id=' + courseId}}>
                        <p className="course-name">{courseName}</p>
                        {/* <p className="course-id ellipsis">{courseId}</p>*/}
                        {/* <p className="course-term ellipsis">{courseTerm}</p>*/}
                    </Link>
                    <Row style={{marginLeft: "9%"}}>
                        <Col span={6}>
                            <Link to={{pathname: '/class', search: '?classId=' + courseId}}>
                                <NotificationOutlined style={{fontSize: 24, color: "rgb(100, 100, 100)"}}/>
                            </Link>
                        </Col>
                        <Col span={6}>
                            <Link to={{pathname: '/class', search: '?classId=' + courseId}}><FolderOutlined
                                style={{fontSize: 24, color: "rgb(100, 100, 100)"}}/>
                            </Link>
                        </Col>
                        <Col span={6}>
                            <Link to={{pathname: '/class', search: '?classId=' + courseId}}><FormOutlined
                                style={{fontSize: 24, color: "rgb(100, 100, 100)"}}/>
                            </Link>
                        </Col>
                        <Col span={6}>
                            <Link to={{pathname: '/class', search: '?classId=' + courseId}}><CommentOutlined
                                style={{fontSize: 24, color: "rgb(100, 100, 100)"}}/></Link>
                        </Col>
                    </Row>
                </div>
            </div>
        );

    }
}

export default CourseCard;

